import React from "react";
// import PropTypes from "prop-types";
import { Carousel, LowMemoryError } from "../../components";
import Button from "@mui/material/Button";
import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import { makeStyles } from "@mui/styles";
// import { getOnboard } from "../../redux/actions";
import { connect } from "react-redux";
import { useNavigate } from "react-router-dom";
import doctorImg from "../../assets/doctors.gif";
import bedImg from "../../assets/bed.png";
import labGIF from "../../assets/laboratorium.gif";
import seePhoneGIF from "../../assets/see_phone.png";
import { Typography } from "@mui/material";

const useStyles = makeStyles({
  rounded: {
    borderRadius: "10px !important",
    width: "100%",
  },
  root: {
    height: "100vh",
  },
});

const OnBoardingPage = (props) => {
  const { error } = props;

  const navigate = useNavigate();

  if (error) {
    navigate("/exception-handling");
  }
  const classes = useStyles();
  const toRoom = () => {
    navigate("/list-room");
  };
  const toRegister = () => {
    navigate("/register");
  };
  const toVaccine = () => {
    navigate("/register-vaksin");
  };
  const toHome = () => {
    navigate("/home");
  };
  React.useEffect(() => {
    // actGetOnboard();
  }, []);
  let carousel = [
    {
      image: doctorImg,
      link: "https://storyset.com/people",
      text: "Rumah Sakit Umum Daerah Cengkareng berkomitmen untuk mengembangkan layanan prima yang mampu bersaing dan menyelenggarakan pendidikan tenaga kesehatan berkualitas",
      title: "Hi, Welcome to RSUD Cengkareng!",
    },
    {
      image: seePhoneGIF,
      link: "https://storyset.com/people",
      text: "Daftar tanpa ribet dari rumah",
      title: "Daftar Online dari Rumah!",
    },
    {
      image: labGIF,
      link: "https://storyset.com/people",
      text: "Daftarkan diri anda untuk melakukan vaksinisasi",
      title: "Layanan Vaksin",
    },
    {
      image: bedImg,
      link: "https://storyset.com/people",
      text: "Lihat data kamar yang tersedia saat ini",
      title: "Online Data Kamar",
    },
  ];
  // carouselArr.map((item) => {
  //   carousel.push(item);
  // });
  const errorAtt = localStorage.getItem("registerAttempt")
    ? parseInt(localStorage.getItem("registerAttempt")) > 0 &&
      parseInt(localStorage.getItem("registerAttempt")) % 3 === 0
      ? true
      : false
    : false;
  const [lowError, setLowError] = React.useState(errorAtt);
  return (
    <Container>
      <LowMemoryError open={lowError} setOpen={setLowError} />

      <Grid
        container
        spacing={4}
        className={classes.root}
        justifyContent="center"
        alignItems="center"
      >
        <Grid item xs={12}>
          <div className={classes.rounded}>
            <Carousel type="big" carousel={carousel} />
          </div>
        </Grid>
        <Grid container item xs={12} spacing={2}>
          {/* <Grid item xs={12}>
            <Button
              variant="contained"
              fullWidth
              onClick={toVaccine}
              className={classes.buttonFoot}
            >
              DAFTAR VAKSIN / VACCINATION
            </Button>
          </Grid>
          <Grid item xs={12}>
            <Button
              variant="contained"
              fullWidth
              onClick={toRegister}
              className={classes.buttonFoot}
            >
              DAFTAR PASIEN BARU / REGISTER
            </Button>
          </Grid>
          <Grid item xs={12}>
            <Button
              variant="contained"
              fullWidth
              onClick={toRoom}
              className={classes.buttonFoot}
            >
              DATA KAMAR / AVAILABLE ROOMS
            </Button>
          </Grid> */}
          <Grid item xs={12}>
            <Button
              variant="contained"
              fullWidth
              onClick={toHome}
              className={classes.buttonFoot}
            >
              LET'S STAY HEALTHY
            </Button>
          </Grid>
          <Grid item xs={12}>
            <Typography variant="body1" align="center">
              &copy;SIM RSUD Cengkareng
            </Typography>
          </Grid>
        </Grid>
      </Grid>
    </Container>
  );
};

const mapStateToProps = (state) => {
  return {
    error: state.app.error,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    // actGetOnboard: (body, action) => () => {},
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(OnBoardingPage);
